import styled from 'styled-components';

export const StyledDiagram = styled.div`
  width: 200px;
  height: 200px;
  background-color: ${({ theme }) => theme.colors.mainColorLight};
  border-radius: 20px;
  box-shadow: ${({ theme }) => theme.boxShadow.mainShadow};
  margin: 5px 0 20px;
  overflow: hidden;
`;
